import moment from 'moment'

const scopes = 'https://www.googleapis.com/auth/calendar https://www.googleapis.com/auth/calendar.events'
const DISCOVERY_DOCS = ['https://www.googleapis.com/discovery/v1/apis/calendar/v3/rest']

const updateSigninStatus = (isSignedIn) => {
    // console.log('login' + isSignedIn)
    // gapi.auth2.getAuthInstance().signOut()
    if (!isSignedIn) {
        // console.log('login' + isSignedIn)
        gapi.auth2.getAuthInstance().signIn()
    }
}

export const initCalendar = () => {
    function start() {
        gapi.client
            .init({
                apiKey: process.env.REACT_APP_GOOGLE_API_KEY,
                client_id: process.env.REACT_APP_CLIENT_ID,
                discoveryDocs: DISCOVERY_DOCS,
                scope: scopes,
            })
            .then(function () {
                // Listen for sign-in state changes.
                gapi.auth2.getAuthInstance().isSignedIn.listen(updateSigninStatus)

                // Handle the initial sign-in state.
                updateSigninStatus(gapi.auth2.getAuthInstance().isSignedIn.get())

            })
    }
    gapi.load('client:auth2', start)
}
export const getEvent = (salesperson) => {

    return new Promise(function (resolve, reject) {

        gapi.client.request({
            path: `https://www.googleapis.com/calendar/v3/calendars/${salesperson.calendarId}/events?maxResults=2500&timeMin=${moment().add(-6, 'months').toISOString()}&timeMax=${moment().add(6, 'months')
                .toISOString()}`,
        }).then(
            response => {
                const events = response.result.items
                events.salesperson = salesperson
                resolve(events)
            },
        ).catch(error => {
            reject(error)
        })

    })
    
}

export const addEvent = (booking) => {

    return new Promise(function (resolve, reject) {
        const endDate = new Date(booking.pick_time)
        // const endDate = new Date(booking.pick_time.getTime())
        // console.log(endDate)
        endDate.setHours(endDate.getHours() + 1)
        
        const event = {
            'summary': booking.address_name,
            'location': booking.address_name,
            'description': `Contact:${booking.full_name}\nPhone:${booking.phone}\n-------------\n${booking.address_name}`,
            'start': {
                'dateTime': booking.pick_time,
                'timeZone': 'Australia/Sydney',
            },
            'end': {
                'dateTime': endDate,
                'timeZone': 'Australia/Sydney',
            },
            'recurrence': [
                
            ],
            'attendees': [
                booking.s_email,
            ],
            'reminders': {
            },
        }

        const request = gapi.client.calendar.events.insert({
            'calendarId': booking.calendarId,
            'resource': event,
        })

        request.execute(function(event1) {
            // console.log('Event created:')
            // console.log(event1)

            if (event1) {
                booking.eid = event1.id
                resolve(booking) 
            } else {
                const error = new Error('failed create event')
                reject(error)
            }
        })
    })
}
