export const suggestAvailableDateTime = (alltime) => {

    for (let i = 0; i< alltime.length; i++) {
        const datelist = alltime[i]
        const bookedTime = datelist.date
        for (let j = 0; j < datelist.timelist.length; j++) {
            const timeItem = datelist.timelist[j]
            if (timeItem.type === 0) {

                const time = timeItem.time

                const year = bookedTime.getFullYear()
                const month = bookedTime.getMonth()
                const date = bookedTime.getDate()

                const timeaar = time.split(':')
                const hour = timeaar[0] - 11
                const minute = timeaar[1]

                const timeslot = new Date(Date.UTC(year, month, date, hour, minute, 0, 0))
                
                return timeslot
            }
        }
    }

    return null
}

export const oneWeekAvailableDateTime = (data) => {
    const availableDates = []
    const endDate= new Date()
    
    let count = 0
    const today = new Date()

    const setting = JSON.parse(localStorage.getItem('setting'))
    
    const noOfDaysToAdd = setting.looking_date
    const dayFactor = setting.day_factor
    // console.log('today is ' + endDate.getDay())
    while (count < noOfDaysToAdd) {
        
        // Date.getDay() gives weekday starting from 0(Sunday) to
        // 6(Saturday)
        
        if (endDate.getDay() !== 0 && endDate.getDay() !== 6 && !isHoliday(endDate)) {
            
            if (endDate.getDate() === today.getDate() && today.getHours() + setting.buffer_time > 4) {//if today && current time + buffer time > 4PM, we need to skip today
                endDate.setDate(endDate.getDate() + 1)
                continue
            }
            const d = new Date(Date.UTC(endDate.getFullYear(), endDate.getMonth(), endDate.getDate(), 0, 0, 0, 0))
            availableDates[count] = d
            count ++
        }

        endDate.setDate(endDate.getDate() + 1)

    }

    const availableDateTime = []

    let possibleTime = possibleTime_15
    if (dayFactor === 15) {
        possibleTime = possibleTime_15
    } else if (dayFactor === 30) {
        possibleTime = possibleTime_30
    } else if (dayFactor === 45) {
        possibleTime = possibleTime_45
    } else if (dayFactor === 60) {
        possibleTime = possibleTime_60
    }

    for (let i = 0; i< availableDates.length; i++) {
        availableDateTime[i] = {}
        availableDateTime[i].date = availableDates[i]

        availableDateTime[i].timelist = []

        let j = 0
        while (j < possibleTime.length) {
            
            availableDateTime[i].timelist[j] = {}
            availableDateTime[i].timelist[j].time = possibleTime[j]

            const bookedType = isBookedAlready(possibleTime[j], availableDates[i], data)
            if (bookedType === 1) {//picked time
                availableDateTime[i].timelist[j].booked = true
                availableDateTime[i].timelist[j].type = 1
            } else if (bookedType === 2) {//meeting time
                availableDateTime[i].timelist[j].booked = true
                availableDateTime[i].timelist[j].type = 2
            } else if (bookedType === 3) {//travling time
                availableDateTime[i].timelist[j].booked = true
                availableDateTime[i].timelist[j].type = 3
            } else if (bookedType === 4) {
                availableDateTime[i].timelist[j].booked = true
                availableDateTime[i].timelist[j].type = 4
            } else if (bookedType === 5) {//not pickable because of today current time ~ 3 hours
                availableDateTime[i].timelist[j].booked = true
                availableDateTime[i].timelist[j].type = 5
            } else {
                availableDateTime[i].timelist[j].booked = false
                availableDateTime[i].timelist[j].type = 0
            }
            j++

        }
    }
    return availableDateTime
}

const isBookedAlready = (time, date, bookData) => {

    // console.log('bookData' + bookData)
    // console.log('compaire date' + date)

    const today = new Date()
    if (today.getDate() === date.getDate()) {//today, need to avoid 3 hours from now
        const year = date.getFullYear()
        const month = date.getMonth()
        const date1 = date.getDate()

        const timeaar = time.split(':')
        const hour = timeaar[0] - 11
        const minute = timeaar[1]

        const timeslot = new Date(Date.UTC(year, month, date1, hour, minute, 0, 0))
        const time_9 = new Date(Date.UTC(year, month, date1, 9-11, 0, 0, 0))
        const basetime = (today.getTime() > time_9) ? today: time_9
        const diffTime = (basetime.getTime() - timeslot.getTime()) / 1000 / 60 //diff minute

        const setting = JSON.parse(localStorage.getItem('setting'))
        if (diffTime > - setting.buffer_time * 60) {//3 hours: current time + 3 hours 
            return 5 // 5 is today current time ~ 3 hours
        }
    }

    for (let i =0; i < bookData.length; i++) {
        const bookedTime = new Date(bookData[i].pick_time)
        // console.log('bookedTime' + bookedTime)
        if (date.getDate() === bookedTime.getDate()) {//same date, need to check the time
            const year = bookedTime.getFullYear()
            const month = bookedTime.getMonth()
            const date1 = bookedTime.getDate()

            const timeaar = time.split(':')
            const hour = timeaar[0] - 11 
            const minute = timeaar[1]

            const timeslot = new Date(Date.UTC(year, month, date1, hour, minute, 0, 0))
            const diffTime = (bookedTime.getTime() - timeslot.getTime()) / 1000 / 60 //diff minute

            if (diffTime === 0) {//picked time
                return 1
            } else if (diffTime < 0 && diffTime >= -75) { // after(meeting + next travel)
                return 2
            } else if (diffTime > 0 && diffTime <= 75) { // before(prvious meeting + travel)
                return 3
            }
        }
    }

    return 0
}

