/**
 * Horizontal Menu
 */
import React, { Component } from 'react'

import { NavLink } from 'react-router-dom'

import NavMenuItem from './NavMenuItem'

import config from 'config'

class HorizontalMenu extends Component {
    componentDidMount() {

    }
    render() {
        return (
            <div className="horizontal-menu">
                <ul className="list-unstyled nav">

                    {
                        config.menus.map((mainmenu, key) => (
                            <li className="nav-item" key={ mainmenu.path }>
                                <NavLink to={ mainmenu.path } className="nav-link">
                                    <i className={ mainmenu.icon }></i>
                                    <span className="menu-title">{mainmenu.title}</span>
                                </NavLink>
                                <ul className="list-unstyled sub-menu">
                                    {mainmenu.submenus 
                                        ? mainmenu.submenus.map((menu, key1) => (
                                            <NavMenuItem
                                                menu={ menu }
                                                // eslint-disable-next-line react/no-array-index-key
                                                key={ key1 }
                                            />
                                        ))
                                        :null}
                                </ul>
                            </li>
                        ))
                    }
                </ul>
            </div>
        )
    }
}

export default HorizontalMenu
