import {history} from '../history'
import moment from 'moment-timezone'
import { NotificationManager } from 'react-notifications'
import { getEvent } from 'Util/Calendar'
import { store } from '../store'

import {
    FIND_TOP_ADDRESS,
    FIND_TOP_ADDRESS_FAILURE,
    LOAD_GLOBAL_SETTING,
    LOAD_GLOBAL_SETTING_SUCCESS,
    LOAD_GLOBAL_SETTING_FAILURE,
    LOAD_SALES_PERSONS,
    LOAD_SALES_PERSONS_SUCCESS,
    LOAD_SALES_PERSONS_FAILURE,
    UPDATE_SETTING_SUCCESS,
    UPDATE_SETTING_FAILURE,
    UPDATE_SETTING,
    ADD_SALES_PERSON,
    ADD_SALES_PERSON_SUCCESS,
    ADD_SALES_PERSON_FAILURE,
    DELETE_SALES_PERSON,
    DELETE_SALES_PERSON_SUCCESS,
    DELETE_SALES_PERSON_FAILURE,
    SUGGEST3_DATETIME,
    SUGGEST3_DATETIME_SUCCESS,
    SUGGEST3_DATETIME_FAILURE,
    POST_BOOKING_SUCCESS,
    POST_BOOKING_FAILURE,
    POST_BOOKING,
    UPDATE_BOOKING,
    UPDATE_BOOKING_SUCCESS,
    UPDATE_BOOKING_FAILURE,
    CLEAR_TOP_ADDRESS,
    LOAD_BOOKING_SUCCESS,
    LOAD_BOOKING_FAILURE,
    LOAD_BOOKING_REQUEST,
    FIND_TOP_SALESPERSON,
    FIND_TOP_SALESPERSON_SUCCESS,
    FIND_TOP_SALESPERSON_FAILURE,

} from 'Actions/types'

import {
    doLoadGlobalSetting,
    doAddSalesperson,
    doLoadSalespersons,
    doDeleteSalesperson,
    doFindTopClosestAddress,
    doSuggest3DateTime,
    doPostbooking,
    doUpdateGlobalSetting,
    doLoadBookings,
    doFindTopClosestSalesperson,
    doUpdatebooking,
    doPostbookingsFromCalendar,
} from 'api/Data'

import {oneWeekAvailableDateTime, suggestAvailableDateTime} from 'Util/DateTime'

function* findTopClosestAddress({ payload }) {
    try {
        // get all booking array -15 days ~ +15 days
        const data = yield call(doFindTopClosestAddress, payload.latitude, payload.longitude)

        // get all closest sales person 
        const data1 = yield call(doFindTopClosestSalesperson, payload.latitude, payload.longitude)

        // get & calculate salesperson who booked within 5 days
        const salesPersonArray = []
        let minCountBooking = Number.MAX_SAFE_INTEGER
        for (let i = 0; i < data1.length; i++) {
            const salesperson = data1[i]
            const bookingArray = data.filter(item => item.group_key === salesperson.sp_pk)
            salesperson.countBooking = bookingArray.length
            salesperson.bookingArray = bookingArray

            if (minCountBooking > salesperson.countBooking) {
                minCountBooking = salesperson.countBooking
            }
            salesPersonArray[i] = salesperson
        }

        let suggestedPersonArray = []
        // remove salesperson who has more than 20% bookings than any other(min count) salesperson
        for (let i = 0; i < salesPersonArray.length; i++) {
            const salesperson = salesPersonArray[i]

            if (salesperson.countBooking <= minCountBooking * 1.2) {
                
                const oneweekDateTime = oneWeekAvailableDateTime(salesperson.bookingArray)
                const suggestDate = suggestAvailableDateTime(oneweekDateTime)
                salesperson.oneweekDateTime = oneweekDateTime
                salesperson.suggestDate = suggestDate
                suggestedPersonArray.push(salesperson)
            }
        }

        // sort by distance
        suggestedPersonArray = suggestedPersonArray.sort((a, b) => {
            return a.distance_in_km - b.distance_in_km
        })

        yield all ([
            yield put( {type: FIND_TOP_SALESPERSON_SUCCESS, payload:{suggestedPersonArray}}),

        ])

    } catch (error) {
        const message = 'Something went wrong'
        yield put( {type: FIND_TOP_ADDRESS_FAILURE, payload: message})
    }
}

function* suggest3DateTime({ payload }) {
    try {
        const data = yield call(doSuggest3DateTime, payload.group_key)

        const oneweekAvailableDateTime = oneWeekAvailableDateTime(data)
        const suggestDates = suggestAvailableDateTime(oneweekAvailableDateTime)
        
        yield all ([
            yield put( {type: SUGGEST3_DATETIME_SUCCESS, payload:{oneweekAvailableDateTime, suggestDates}}),
        ])

    } catch (error) {
        const message = 'Something went wrong'
        yield put( {type: SUGGEST3_DATETIME_FAILURE, payload: message})
    }
}

function* findTopClosestSalesperson({ payload }) {
    try {
        
        const data1 = yield call(doFindTopClosestSalesperson, payload.latitude, payload.longitude, 5)

        yield all ([
            yield put( {type: FIND_TOP_SALESPERSON_SUCCESS, payload:{data1}}),
        ])

    } catch (error) {
        const message = 'Something went wrong'
        yield put( {type: FIND_TOP_SALESPERSON_FAILURE, payload: message})
    }
}

function* loadGlobalSetting() {
    try {
        const data = yield call(doLoadGlobalSetting)
        
        yield all ([
            yield put( {type: LOAD_GLOBAL_SETTING_SUCCESS, payload:{data}}),
            localStorage.setItem('setting', JSON.stringify(data[0])),
        ])

    } catch (error) {
        const message = 'Something went wrong'
        yield put( {type: LOAD_GLOBAL_SETTING_FAILURE, payload: message})
    }
}

function* updateGlobalSetting({ payload }) {
    try {
        const data = yield call(doUpdateGlobalSetting, payload.g_setting)
        yield all ([
            yield put( {type: UPDATE_SETTING_SUCCESS, payload:{data}}),
            yield put( {type: LOAD_GLOBAL_SETTING, payload:{}}),
        ])

    } catch (error) {
        const message = 'Something went wrong'
        yield put( {type: UPDATE_SETTING_FAILURE, payload: message})
    }
}

function* loadSalespersons() {
    try {
        const data = yield call(doLoadSalespersons)

        for (let i=0; i<data.length; i++) {
            getEvent(data[i])
                .then(eventData => {

                    const bookingsData = store.getState().allData.bookingsData

                    let bookingData = []
                    for (let j = 0; j < eventData.length; j++) {
                        const eventItem = eventData[j]
                        const findBooking  = bookingsData.find(bookingItem => eventItem.id === bookingItem.eid)

                        if (!findBooking) {

                            const eventDescription = eventItem.description
                            let phoneNumber = ''
                            let contactName = ''

                            if (eventDescription) {
                                if (eventDescription.match(/Phone:(.*?)\\n/)) {
                                    phoneNumber = eventDescription.match(/Phone:(.*?)\\n/)[0].replace('Phone:', '').replace('\\n', '').trim()
                                    contactName = eventDescription.match(/Contact:(.*?)\\n/)[0].replace('Contact:', '').replace('\\n', '').trim()
                                } else if (eventDescription.match(/Phone:(.*?)\n/)) {

                                    phoneNumber = eventDescription.match(/Phone:(.*?)\n/)[0].replace('Phone:', '').replace('\n', '').trim()
                                    contactName = eventDescription.match(/Contact:(.*?)\n/)[0].replace('Contact:', '').replace('\n', '').trim()
                                }
                            }

                            let pickTime = eventItem.start.dateTime
                            if (!pickTime) {
                                pickTime = `${eventItem.start.date}T09:00:00+11:00`
                            }
                            pickTime = new Date(pickTime)

                            if (!pickTime) {
                                continue
                            }
                            const booking = {
                                full_name: contactName,
                                email: '',
                                phone: phoneNumber,
                                pick_time: pickTime,
                                address_name: eventItem.summary,
                                latitude: 33.8688,
                                longitude: 151.2093,
                                group_key: eventData.salesperson.sp_pk,
                                eid: eventItem.id,
                            }
        
                            bookingData = [...bookingData, booking]

                            if (bookingData.length > 100) {
                                doPostbookingsFromCalendar(bookingData)
                                bookingData = []
                            }
                            
                        }

                    }
                    if (bookingData.length > 0)
                        doPostbookingsFromCalendar(bookingData)

                } )
        }
        yield all ([
            yield put( {type: LOAD_SALES_PERSONS_SUCCESS, payload:{data}}),
        ])

    } catch (error) {
        const message = 'Something went wrong'
        yield put( {type: LOAD_SALES_PERSONS_FAILURE, payload: message})
    }
}

function* loadBookings() {
    try {
        const data = yield call(doLoadBookings)

        const arraydata = [] 
        
        for (let j = 0; j < data.length; j ++) {
            let subitem = Object.values(data[j])
            subitem[4] = moment(subitem[4]).tz('Australia/Sydney').format('MMM DD YYYY, hh:mm a')
            subitem = subitem.slice(0, 10)
            arraydata.push(subitem)
        }
    
        yield all ([
            yield put( {type: LOAD_BOOKING_SUCCESS, payload:{arraydata, data}}),
            yield put( {type: LOAD_SALES_PERSONS, payload:{}}),
        ])

    } catch (error) {
        const message = 'Something went wrong'
        yield put( {type: LOAD_BOOKING_FAILURE, payload: message})
    }
}

function* addSalesperson({ payload }) {
    try {
        const data = yield call(doAddSalesperson, payload.salesperson)
       
        getEvent(data[0])
            .then(eventData => {

                const bookingsData = store.getState().allData.bookingsData

                let bookingData = []
                for (let j = 0; j < eventData.length; j++) {
                    const eventItem = eventData[j]
                    const findBooking  = bookingsData.find(bookingItem => eventItem.id === bookingItem.eid)

                    if (!findBooking) {

                        const eventDescription = eventItem.description
                        let phoneNumber = ''
                        let contactName = ''

                        if (eventDescription) {
                            if (eventDescription.match(/Phone:(.*?)\\n/)) {
                                phoneNumber = eventDescription.match(/Phone:(.*?)\\n/)[0].replace('Phone:', '').replace('\\n', '').trim()
                                contactName = eventDescription.match(/Contact:(.*?)\\n/)[0].replace('Contact:', '').replace('\\n', '').trim()
                            } else if (eventDescription.match(/Phone:(.*?)\n/)) {

                                phoneNumber = eventDescription.match(/Phone:(.*?)\n/)[0].replace('Phone:', '').replace('\n', '').trim()
                                contactName = eventDescription.match(/Contact:(.*?)\n/)[0].replace('Contact:', '').replace('\n', '').trim()
                            }
                        }

                        let pickTime = eventItem.start.dateTime
                        if (!pickTime) {
                            pickTime = `${eventItem.start.date}T09:00:00+11:00`
                        }
                        pickTime = new Date(pickTime)

                        if (!pickTime) {
                            continue
                        }
                        const booking = {
                            full_name: contactName,
                            email: '',
                            phone: phoneNumber,
                            pick_time: pickTime,
                            address_name: eventItem.summary,
                            latitude: 33.8688,
                            longitude: 151.2093,
                            group_key: eventData.salesperson.sp_pk,
                            eid: eventItem.id,
                        }
    
                        bookingData = [...bookingData, booking]

                        if (bookingData.length > 100) {
                            doPostbookingsFromCalendar(bookingData)
                            bookingData = []
                        }
                        
                    }

                }
                doPostbookingsFromCalendar(bookingData)

            } )
        yield all ([
            yield put( {type: ADD_SALES_PERSON_SUCCESS, payload:{data}}),
            yield put( {type: LOAD_SALES_PERSONS, payload:{}}),
        ])

    } catch (error) {
        const message = 'Something went wrong'
        yield put( {type: ADD_SALES_PERSON_FAILURE, payload: message})
    }
}

function* deleteSalesperson({ payload }) {
    try {
        const data = yield call(doDeleteSalesperson, payload.sp_pk)
        yield all ([
            yield put( {type: DELETE_SALES_PERSON_SUCCESS, payload:{data}}),
            yield put( {type: LOAD_SALES_PERSONS, payload:{}}),
        ])

    } catch (error) {
        const message = 'Something went wrong'
        yield put( {type: DELETE_SALES_PERSON_FAILURE, payload: message})
    }
}

function* postbooking({ payload }) {
    try {
        const data = yield call(doPostbooking, payload.booking)
        yield all ([
            yield put( {type: POST_BOOKING_SUCCESS, payload:{data}}),
            yield put( {type: CLEAR_TOP_ADDRESS, payload:{}}),
            NotificationManager.info('Booking submitted successfully'),
            history.goBack(),
        ])

    } catch (error) {
        const message = 'Something went wrong'
        yield put( {type: POST_BOOKING_FAILURE, payload: message})
    }
}

function* updatebooking({ payload }) {
    try {
        const data = yield call(doUpdatebooking, payload.booking)
        yield all ([
            yield put( {type: UPDATE_BOOKING_SUCCESS, payload:{data}}),
            NotificationManager.info('Booking updated successfully'),
        ])

    } catch (error) {
        const message = 'Something went wrong'
        yield put( {type: UPDATE_BOOKING_FAILURE, payload: message})
    }
}

/*
 * Auth Root Saga
 */
export default function* rootSaga() {
    yield all([
        takeEvery(FIND_TOP_ADDRESS, findTopClosestAddress),
        takeEvery(LOAD_BOOKING_REQUEST, loadBookings),
        takeEvery(LOAD_GLOBAL_SETTING, loadGlobalSetting),
        takeEvery(UPDATE_SETTING, updateGlobalSetting),
        takeEvery(LOAD_SALES_PERSONS, loadSalespersons),
        takeEvery(ADD_SALES_PERSON, addSalesperson),
        takeEvery(DELETE_SALES_PERSON, deleteSalesperson),
        takeEvery(SUGGEST3_DATETIME, suggest3DateTime),
        takeEvery(POST_BOOKING, postbooking),
        takeEvery(FIND_TOP_SALESPERSON, findTopClosestSalesperson),
        takeEvery(UPDATE_BOOKING, updatebooking),
    ])
}
